
// 顶部导航栏和其他所有的组件通信
export default{
    namespaced:true,//带命名空间
    state:{
      /*_plus对象 */
      _plus : null,
      // 用户是否登录
      hasLogin : false,
      //是否第一次进入网页并且有token 在检查完token后使用
      HasTokenThenFirst : true,
      //用户信息
      userInfo : {},
	  //网络加载
	  loadingForHttp:false
    },
    // params
    mutations:{
      _plusHandler(state,params){
          state._plus = params
      },
      //网络加载
      loadingForHttpHandler(state,params){
          state.loadingForHttp = params
      },
        // 用户是否登录函数
        hasLoginHandler(state,params){
            state.hasLogin = params
        },
        // 有token然后第一次进入网页
        HasTokenThenFirstHandler(state,params){
          state.HasTokenThenFirst = params
        },
      // 用户信息
      userInfoHandler(state,params){
        /* 字符串转化数字*/
        params.user_lv = parseInt(params.user_lv)
        params.user_gold = parseInt(params.user_gold)
        state.userInfo = params
        console.log(state.userInfo,"用户信息")
      },
      //删除用户信息
      delUserInfoHandler(state,params){
        state.userInfo = {}
        console.log(state.userInfo,"删除了用户信息")
      },
      // 修改用户信息 this.setHandler(['user_gold',tempVar])
      setHandler(state,params){
        /* 如果是修改用户文章计数*/
        if(params[0] == 'user_article_count'){
          if(params[1] == 'add'){
            state.userInfo.user_article_count ++
          }else{
            state.userInfo.user_article_count --
          }
        }else if(params[0] == 'user_col_count'){
          /* 如果是修改用户收藏计数*/
          if(params[1] == 'add'){
            state.userInfo.user_col_count ++
          }else{
            state.userInfo.user_col_count --
          }
        }else if(params[0] == 'user_unread'){
          /* 如果是修改用户信息计数*/
          if(params[1] == 'add'){
            state.userInfo.user_unread ++
          }else if(params[1] == 'jian'){
            state.userInfo.user_unread  = 0
          }
        }else{
          state.userInfo[params[0]] =  params[1]
        }

      },
    },
    actions:{

    },
    getters:{

    }

}



