<template>
    <!-- <SignUp></SignUp> -->
    <!-- <van-grid square >
      <van-grid-item @click='goPlate(index)'
        v-for="(item, index) in grid"
        :key="index"
        :icon="item.icon"
        :text="item.text"
      />
    </van-grid> -->
    <div class="grid am-grid">
      <div class="item" @click='goPlate(index)'
      v-for="(item, index) in grid" :key="item.article_id">
        <img :src="item.icon" alt="">
        <span>{{item.text}}</span>
      </div>
    </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/grid"

  import {createNamespacedHelpers} from 'vuex'
  const grid=createNamespacedHelpers('grid')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        // 宫格
        // grid: [
        //   {
        //     icon:'https://dss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2616061903,1825516655&fm=26&gp=0.jpg',
        //     text:'王者荣耀'
        //   },
        //   {
        //     icon:'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2271364860,1931030529&fm=15&gp=0.jpg',
        //     text:'和平精英'
        //   },
        //   {
        //     icon:"http://img1.imgtn.bdimg.com/it/u=1619639147,2014606316&fm=26&gp=0.jpg",
        //     text:'qq飞车'
        //   },
        //   {
        //     icon:"http://img1.imgtn.bdimg.com/it/u=2518151570,11227073&fm=26&gp=0.jpg",
        //     text:'穿越火线'
        //   }
        // ]
      }
    },
    /* 方法*/
    methods:{
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
      goPlate(index){
        this.$router.push({
          path:'/plate',
          query : {
            plate : index
          }

        })
      }

    },
    /* 计算*/
    computed:{
     ...grid.mapState({
          	grid : state => state.grid
          })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .grid{
    background-color: white;
    width: 960px;
    @include flex(row ,flex-start,center);
    margin : 0 auto;
    margin-bottom:10px;
    flex-wrap:wrap ;
    .item{
      font-size: 18px;
      &:hover{
        font-size: 28px;
      }
      transition: all .3s;
      // background-color: red;
      cursor: pointer;
      @include wh(192px,160px)
      // flex-basis : 240px;//(根据当时宽度来决定)
      @include flex(column,center,center);
      img{
        object-fit:cover;
        border-radius: 50%;
        @include wh(80px,80px)
      }
      span{
        margin-top: 10px;
        font-weight: 600;
      }
    }
  }
</style>



