
// 顶部导航栏和其他所有的组件通信
export default{
    namespaced:true,//带命名空间
    state:{
      // 浏览器窗口
      winWH : {
          width:undefined,
          height:undefined
      },
      isBuffering : false,
      isDoing : false,
      // NotFound :false,
    },
    // params
    mutations:{
      // fromPath
      winWHHandler(state,params){
        let pageWidth = window.innerWidth,
            pageHeight = window.innerHeight;

        if (typeof pageWidth != "number"){
            if (document.compatMode == "CSS1Compat"){
                pageWidth = document.documentElement.clientWidth;
                pageHeight = document.documentElement.clientHeight;
            } else {
                pageWidth = document.body.clientWidth;
                pageHeight = document.body.clientHeight;
            }
        }

        state.winWH.width = pageWidth + "px"
        state.winWH.height = pageHeight + "px"

        // console.log(state.winWH,'state.winWH')
        // const windowWidth = document.documentElement.clientWidth
        // // window.innerWidth
        // // document.body.clientWidth
        // const windowHeight = document.documentElement.clientHeight
        // state.winWH.width = windowWidth + "px"
        // state.winWH.height = windowHeight + "px"
      },
      isBufferingHandler(state,params){
        state.isBuffering = params
      },
      isDoingHandler(state,params){
        state.isDoing = params
      },
      // NotFoundHandler(state,params){
      //   state.NotFound = params
      // }

    },
    actions:{

    },
    getters:{

    }

}



