/**
 * axios封装第三步
  * 模块的管理
  */

import ipObj from '../ipObj'; // 导入接口域名列表
import axios from '@/axios/http'; // 导入http中创建的axios实例
// import qs from 'qs'; // 根据需求是否导入qs模块

const login = {
  // 测试登录
  testLogin(params){
    return axios.post(`${ipObj.test}/login/testLogin`,params)
  },
  // 发送验证码
	sendCode(params){
    return axios.post(`${ipObj.test}/login/sendCode`,params)
  },
  // 登录用户
  userLogin(params){
    return axios.post(`${ipObj.test}/login/userLogin`,params)
  },
  // 获得用户信息
  /*
  前端在router的index.js
  不需要判断是否登录
  后端 在 login 的userInfo文件夹的getUserInfo.js
  */
  getUserInfo(params){
    return axios.post(`${ipObj.test}/login/getUserInfo`,params)
  },

}

export default login;



