<template>
  <div class="topBoss">
  <div class="top">
    <!-- <SignUp></SignUp> -->
    <div class="left" @click="toHomePage">
      简书
    </div>

    <div class="right" >
      <!-- 未登录-->
      <div class="noLogin" v-if='!hasLogin'>
        <div @click="toLogin" class="login">登录</div>
        <div @click="toLogin" class="signUp">注册</div>
      </div>

      <!-- 已经登录-->
      <div class="hasLogin" v-if='hasLogin'>
        <!-- <span>欢迎&nbsp;&nbsp;{{userInfo.user_name}}</span> -->
        <div class="search" @click="toSearch">搜索</div>
        <img :src="userInfo.user_img" alt="" @click="toUser">
      </div>
    </div>
  </div>
  
  </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {

      }
    },
    /* 方法*/
    methods:{
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
      /* 去首页*/
      toHomePage(){
        this.$router.push({
          path : '/'
        })
      },
      /* 去登录页*/
      toLogin(){
        this.$router.push({
          path : '/login'
        })
      },
      /* 去用户页*/
      toUser(){
        this.$router.push({
          path : '/user'
        })
      },
      /* 去搜索页*/
      toSearch(){
        this.$router.push({
          path : '/search'
        })
      }
    },
    /* 计算*/
    computed:{
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
      ...login.mapState({
          	hasLogin : state => state.hasLogin
      }),
      ...login.mapState({
           	userInfo : state => state.userInfo
      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .topBoss{
    width: 100%;
    background-color: white;
    margin-bottom: 10px;
  }
   .top{
    margin: 0 auto;
    @include wh(960px,50px);
    @include flex(row,space-between,center);
     .left{
       color:#ec7259;
       font-size: 40px;
       @include wh(160px,100%);
       @include flex(row,flex-start,center);
       // background-color: red;
     }

     .right{
       font-size: 20px;
      .noLogin{
        @include wh(400px,100%);
        @include flex(row,space-between,center);
        .login{
          color:#969696;
          @include wh(100px,40px);
          @include flex(row,center,center);
        }
        .signUp{
          color:#ec7259;
          @include wh(100px,40px);
          border:1px solid #ec7259;
          @include flex(row,center,center);
          border-radius: 50px;
        }
      }

      .hasLogin{
        // background-color: blue;
        font-size: 36px;
        color : #999;
        @include wh(480px,100%);
        @include flex(row,space-between,center);
        .search{
          @include wh(300px,40px);
          background-color: #f2f2f2;
          border-radius: 80px;
          font-size: 20px;
          line-height: 60px;
          padding-left: 20px;
        }
        img{
          box-shadow: 0 0 14px #888;
          border-radius: 50%;
          @include wh(40px,40px);
          object-fit: cover;
        }
      }


     }
   }
</style>



