/**
 * axios封装第三步
  * 模块的管理
  */

import ipObj from '../ipObj'; // 导入接口域名列表
import axios from '@/axios/http'; // 导入http中创建的axios实例
// import qs from 'qs'; // 根据需求是否导入qs模块

const user = {
	// 设置用户标签
	/*
  前端在utils的setOtherTag组件里面
  判断是否登录 不带跳转提示
  后端在 user 的 otherUse 的setOtherTag.js
  */
	setOtherTag(params){
	  return axios.post(`${ipObj.test}/user/setOtherTag`,params)
	},

  // 获得用户信息
  
	// getUserInfo(params){
 //    return axios.post(`${base.test}/user/getUserInfo`,params)
 //  },
  // 用户修改头像
  headPor(params){
    return axios.post(`${ipObj.test}/user/headPor`,params)
  },
  // 用户修改昵称
  mName(params){
    return axios.post(`${ipObj.test}/user/mName`,params)
  },
 // 签到
 checkTime(params){
   return axios.post(`${ipObj.test}/user/checkTime`,params)
 },
 // 用户通知信息
 info(params){
   return axios.post(`${ipObj.test}/user/info`,params)
 },
 // 用户通知信息
 infoMore(params){
   return axios.post(`${ipObj.test}/user/infoMore`,params)
 },
 // 其他用户信息
 otherUser(params){
   return axios.post(`${ipObj.test}/user/otherUser`,params)
 },
 //主动关注其他用户
 follow(params){
   return axios.post(`${ipObj.test}/user/follow`,params)
 },
 //get是否关注了文章作者
 getFollow(params){
   return axios.post(`${ipObj.test}/user/getFollow`,params)
 },
 //关注和粉丝
 folAndFans(params){
   return axios.post(`${ipObj.test}/user/folAndFans`,params)
 },
 //关注和粉丝
 folAndFansMore(params){
   return axios.post(`${ipObj.test}/user/folAndFansMore`,params)
 },
 //其他人关注和粉丝
 folAndFansForOther(params){
   return axios.post(`${ipObj.test}/user/folAndFansForOther`,params)
 },
 //其他人关注和粉丝
 folAndFansForOtherMore(params){
   return axios.post(`${ipObj.test}/user/folAndFansForOtherMore`,params)
 },
}

export default user;



