
// 顶部导航栏和其他所有的组件通信
export default{
    namespaced:true,//带命名空间
    state:{
      //首页和palte 搜索
      one : ['homePage','more'],
      // 个人消息 固定的
      info : ['info','infoMore'],
      //评论  固定的
      comReq : ['getComment','getCommentMore'],
      // 其他用户信息
      // other:['1','2'],
      // 关键词 搜索页
      KeyWord : "",
      // 是否停止加载事件
      //搜索 重置list
      Reset : false,
      // 文章列表
      // list : [],
	  // 首页的下拉刷新
	  isLoadingForHomePage : false,
    /* sort 默认值*/
    value1 : 'article_comment_time',
    /* 文章评论排序commentSort*/
    commentSort : 'last',
    // 只看楼主
    // lookAuthor:false
    },

    // params
    mutations:{
      // lookAuthorHandler(state, params){
      //   state.lookAuthor = params
      // },
      commentSortHandler(state, params){
        state.commentSort = params
      },
      // sort
      value1Handler(state, params){
          state.value1 = params
      },
      //首页的下拉刷新
      isLoadingForHomePageHandler(state, params){
          state.isLoadingForHomePage = params
      },
      // 展示评论框
      oneHandler(state, params){
        state.one = params
        console.log(state.one,'state.one')
      },
      // 展示评论框
      // infoHandler(state, params){
      //   state.one = params
      //   console.log(state.one,'state.one')
      // },
      // 展示评论框
      // otherHandler(state, params){
      //   state.other = params
      //   console.log(state.other,'state.other')
      // },
      // 关键词
      KeyWordHandler(state, params){
        state.KeyWord = params
      },
      ResetHandler(state, params){
        state.Reset = params
        console.log(state.Reset)
      },
      // listHandler(state, params){
      //   state.list = params
      // }
    },
    actions:{

    },
    getters:{

    }

}



