// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
/* ***************************************************

 */
import globalVal from './assets/fn/globalVal'
import VueSocketIO from 'vue-socket.io'
Vue.use(new VueSocketIO({
    debug: true,
//    connection: 'http://localhost:3131', 
    connection: `${globalVal.socketIOPort}`, 
    // connection: 'http://154.83.15.180:3131',
    options: {
      autoConnect : false,
      query:{
          token: ''
         }
    }
}))
//使用Vue.mixin的方法拦截了路由离开事件，并在该拦截方法中实现了销毁页面缓存的功能。
// import clearCahce from './assets/fn/clearCahce'
import clearCahce from './assets/fn/clear'
// Vue.mixin(clearCahce)

/* 不知道这是干嘛的 */
Vue.config.productionTip = false
/* eslint-disable no-new */
// import clearEnter from './assets/fn/clearEnter'
// Vue.mixin(clearEnter)

/* *******************************************8
图形验证
 */
// import PuzzleVerification from 'vue-puzzle-verification'
// Vue.use(PuzzleVerification)
/* *********************************************************************
 vant
 */
import '@vant/touch-emulator';
//弹出框
 import { Dialog } from 'vant';
 // Vue.prototype.Dialog = Dialog;
 Vue.use(Dialog);
/* 输入框 */
import { Field } from 'vant';
Vue.use(Field);
/* 单元格 */
import { Cell, CellGroup } from 'vant';
Vue.use(Cell).use(CellGroup);
/* 按钮 */
import { Button } from 'vant';
Vue.use(Button);
/* 倒计时 */
import { CountDown } from 'vant';
Vue.use(CountDown);
/* 弹出层 */
import { Popup } from 'vant';
Vue.use(Popup);
/* 选择器 */
import { Picker } from 'vant';
Vue.use(Picker);
/* 列表 */
import { List } from 'vant';
Vue.use(List);
/* 标签页 */
import { Tab, Tabs } from 'vant';
Vue.use(Tab).use(Tabs);
/* 下拉刷新 */
import { PullRefresh } from 'vant';
Vue.use(PullRefresh);
/* 文件上传 */
import { Uploader } from 'vant';
Vue.use(Uploader);
/*轻提示  */
import { Toast } from 'vant';
Vue.use(Toast);
/* 轮播 */
import { Swipe, SwipeItem } from 'vant';
Vue.use(Swipe).use(SwipeItem);
/* *********************************************************************
 解决ie空白
 */
import 'babel-polyfill'
/* ********************************************************************
*引入ali图标
 */
import './assets/iconfont.css'

import './assets/UIstyle.css'

/* *********************************************************************
 引入vuex
 */
import store from './vuex/store'

/* *********************************************************************
 将api挂载vue原型上
 */
import api from './axios/api/index'
Vue.prototype.$api = api;
/* ********************************************************************

 */
import formatDate from './assets/fn/formatDate'
Vue.prototype.$formatDate = formatDate;
/* *********************************************************************
 vue项目报错vue-router.esm.js   无需安装其他包
 */
import VueRouter from 'vue-router';
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error=> error)
}

/* *********************************************************************
 tip
 */
import msg from './assets/fn/msg'
Vue.prototype.$tip = msg;
// /* ***********************************
// 计算图片大小
// */
// import computedImg from './assets/fn/computed/computedImg'
// Vue.prototype.$computedImg = computedImg;
/*
 全局过滤器
 */
import fn_fTime from './assets/fn/filter/fTime'
Vue.filter('fTime', fn_fTime)
/* 没有文章 */
import fn_fNoArticle from './assets/fn/filter/fNoArticle'
Vue.filter('fNoArticle', fn_fNoArticle)
/* 没有评论 */
import fn_fNoComment from './assets/fn/filter/fNoComment'
Vue.filter('fNoComment', fn_fNoComment)
/* 根据经验计算等级 */
import fn_fLvComputed from './assets/fn/filter/fLvComputed'
Vue.filter('fLvComputed', fn_fLvComputed)
/* 等级加1 */
import fn_fLvComputedAdd from './assets/fn/filter/fLvComputedAdd'
Vue.filter('fLvComputedAdd', fn_fLvComputedAdd)
/* 是最新文章还是最新评论 */
import fn_fNewAOrC from './assets/fn/filter/fNewAOrC'
Vue.filter('fNewAOrC', fn_fNewAOrC)

/* *********************************** */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



