<template>
  <div id="app">

     <Top></Top>

    <!-- 缓冲层-->
    <buffering ></buffering>
    <!-- 提交层-->
    <Doing></Doing>
    <!-- <PopupForReward></PopupForReward> -->
    <!-- <popupForBidding></popupForBidding> -->
    <!-- <Popup></Popup> -->
    <!-- 顶部导航栏 -->
     <Grid v-show = '$route.path == "/"'></Grid>
    <div class="pc_content">
    <!-- 视口 -->
    <keep-alive>
      <router-view name='com' v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>

    <router-view name='com' v-if="!$route.meta.keepAlive"></router-view>
    <div class="pc_right"></div>
    </div>



    <!-- 底部标签栏 -->
    <!-- <TabBar></TabBar> -->
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  const win=createNamespacedHelpers('win')

  /* 引入宫格组件*/
  import Grid from "@/components/utils/Grid"
  import buffering from "@/components/utils/buffering"
  import Doing from "@/components/utils/Doing"
  // import {mapState,mapMutations} from 'vuex'
  /* 引入标签栏组件*/
  // import Popup from "@/components/utils/Popup"
  // import PopupForReward from "@/components/utils/PopupForReward"
  // import popupForBidding from "@/components/utils/popupForBidding"
  /* 引入导航栏组件*/
  // import NavBar from "@/components/utils/NavBar"
  import Top from "@/components/utils/Top"

  /* 引入标签栏组件*/
  // import TabBar from "@/components/utils/TabBar"
export default {
  name: 'App',
  methods : {
    ...win.mapMutations({
          	winWHHandler: 'winWHHandler'
          }),
    /* 修改用户信息方法*/
    ...login.mapMutations({
            setHandler: 'setHandler'
          }),
  },
  components : {
  	/* 这个组件名字不能和原生标签名重复 */
    // TabBar,
    // NavBar,
    // Popup,
    // PopupForReward,
	// popupForBidding,
  Top,
  buffering,
  Doing,
  Grid
  },
  sockets:{
    connect(){
      console.log('连接成功')
    },
    //重连成功 // 断开服务器端自动执行
    reconnect(){
      console.log('重连成功')
    },
    // 重连中...... 断开服务器端自动执行
    reconnecting(){
      console.log('重连中......')
    },
    //断开连接
    disconnect(data){
      // this.$socket.io.opts.query = {
      //     token:'fgh'
      //    }
      console.log('连接断开',this.$socket)
    },
    //自定义///////////////////////////////////////////////
    // returnMsg(data){
    //   this.$tip(data.code,'success')
    //   this.everyMsg = this.everyMsg.concat(data)
    //   this.message = ''
    // },
    every(data){
      // this.everyMsg = this.everyMsg.concat(data)
      this.$toast(data)
    },
    /* 打赏*/
    reward(data){
      this.$toast(data)
    },
    /* 评论*/
    comment(data){
      this.$toast(data)
    },
    /* 有新消息*/
    newInfo(data){
      this.setHandler(['user_unread','add'])
      switch(data){
        case 'commentForComment':
          this.$toast('有人评论了您的评论')
        break;
        case 'commentForArticle':
          this.$toast('有人评论了您的文章')
        break;
        case 'reward':
          this.$toast('有人打赏了您的文章')
        break;
      }
    }
    // error(data){
    //   console.log("io错误信息:",err);
    // }
  },
  computed:{
	...login.mapState({
	     	userInfo : state => state.userInfo
	     }),

  },
  beforeDestroy() {
    // 移除窗口大小事件
   window.removeEventListener('resize', this.winWHHandler)
  },
  mounted() {
    // 先执行窗口函数
    this.winWHHandler()
      // 绑定窗口大小事件
    window.addEventListener('resize', this.winWHHandler)
    /* *********************************************/
  },
  watch:{
    'userInfo.user_id': {
        handler(newVal, oldVal) {
          if(!!newVal == true){
            console.log(newVal,'用户id--------------')
            this.$socket.io.opts.query = {
                token:newVal
               }
            this.$socket.open()
          }

        },
        // immediate: true,
        // deep: true
    }
  }
}
</script>

<style scoped lang="scss">
  #app{
  background-color: $jsBgcGray;
  .pc_content{
      margin: 0 auto;
      position : relative;
      width: 960px;
      // background-color: green;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
  	.pc_right{
  		@include wh(280px,1000px);
  		background-color: red;
  	}
    }
  }
</style>



