
// 顶部导航栏和其他所有的组件通信
export default{
    namespaced:true,//带命名空间
    state:{
      /* 首页*/
       noticeArr: [],
       // curArtBidding_price:0,
       /* 板块*/
       noticePlateArr: [],
       // curArtBiddingPlate_price:0
    },

    // params
    mutations:{
      /* 首页*****************************************/
      noticeArrHandler(state, params){
         // alert('执行1')
        for(let i= 0;i<params.length;i++){
          if(!params[i].tit){
            // params.splice(i,1)
            params[i].bidding_article_id = 0
            // params[i].tit = '文章飞到其他星球了'
          }
        }
         state.noticeArr = params
      },
      // curArtBidding_priceHandler(state, params){
      //   state.curArtBidding_price = params
      // },
      /* 板块*****************************************/
      noticePlateArrHandler(state, params){
        // alert('执行2')
        for(let i= 0;i<params.length;i++){
          if(!params[i].tit){
            // params.splice(i,1)
            params[i].bidding_article_id = 0
            // params[i].tit = '文章飞到其他星球了'
          }
        }
         state.noticePlateArr = params
      },
      // curArtBiddingPlate_priceHandler(state, params){
      //   state.curArtBiddingPlate_price = params
      // }

    },
    actions:{

    },
    getters:{

    }

}



