  function fTime(value){
        if(new Date().getFullYear()-new Date(value).getFullYear()>=1){
          // 现在的年份减去文章发表的年份大于一
          return value;
        }else if(new Date().getMonth()-new Date(value).getMonth()>=1){
          // 现在的玥份减去文章发表的月份大于等于一
          return new Date(value).getMonth()+1+'月'+new Date(value).getDate()+'号'+value.slice(10,16)
        }
        else if(new Date().getDate()-new Date(value).getDate()>2){
          // 大于两天
          return new Date(value).getDate()+'号'+value.slice(10,16)
        }else if(new Date().getDate()-new Date(value).getDate()==2){
          //等于两天
          return '前天'+value.slice(10,16)
        }else if(new Date().getDate()-new Date(value).getDate()==1){
          //等于一天
          return '昨天'+value.slice(10,16)
        }else if(new Date().getDate()-new Date(value).getDate()==0){
          //今天
          return '今天'+value.slice(10,16)
        }
      }

      export default fTime;



