<template>
  <div class="buffering" v-show='isBuffering'>
    <div class="load_more_box">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
    </div>
  </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  // import {createNamespacedHelpers} from 'vuex'
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"buffering",
    /* 数据*/
    data(){
      return {

      }
    },
    /* 方法*/
    methods:{
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
    },
    /* 计算*/
    computed:{
      isBuffering:{
        get () {
              return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
        },
        set (value) {
          this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
        }
      },
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
.buffering{
  @include wh(100%,100%);
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999999999999999999;
  @include flex(row,center,center);
}
/* *********************************** */
/* 上拉加载 */
	.load_more_box {
	height:40px;
	line-height:40px;
	position:relative;
	text-align:center;
}
.load_more_box span {
	width:5px;
	height:5px;
	margin:0 1px;
	display:inline-block;
	vertical-align:middle;
	background:#9b59b6;
	animation:load_more_box 1.5s  infinite ease-in-out;
}
.load_more_box span:nth-child(2) {
	animation-delay:.2s;
}
.load_more_box span:nth-child(3) {
	animation-delay:.4s;
}
.load_more_box span:nth-child(4) {
	animation-delay:.6s;
}
.load_more_box span:nth-child(5) {
	animation-delay:.8s;
}
@keyframes load_more_box {
	0% {
	height:5px;
	background:#9b59b6;
}
25% {
	height:30px;
	background:#0081ff;
}
50% {
	height:5px;
	background:#9b59b6;
}
100% {
	height:5px;
	background:#9b59b6;
}
}
</style>



