import Vue from 'vue'
import Router from 'vue-router'


/* 引入axios */
import api from '../axios/api/index'

 /* 引入vuex */
import store from '../vuex/store'

import { Toast } from 'vant';
// 例子
// store.commit('qwe', null);
// store._modulesNamespaceMap['test/'].context.commit('testDemo', null);
// console.log(store,888)

Vue.use(Router)

const router = new Router({
  scrollBehavior (to, from, savedPosition) {
      if (savedPosition) {
        return savedPosition
      } else {
        return { x: 0, y: 0 }
      }
    },
  routes: [
    // 其他人的关注和粉丝
    {
      path: '/otherForFAndF',
      name: 'otherForFAndF',
      components:{
        com :() => import('@/components/me/utils/FolAndFansForOther')
      },
      meta: {
          keepAlive: true,
         }
    },
    /* 其他人的收藏和文章*/
    {
      path: '/otherForAAndC',
      name: 'otherForAAndC',
      components:{
        com :() => import('@/components/utils/ListForOtherUser')
      },
      meta: {
          keepAlive: true,
         }
    },
    /* 有权限的人处理举报*/
    {
      path: '/report',
      name: 'report',
      components:{
        com :() => import('@/components/utils/Report')
      },
      meta: {
          keepAlive: true,
         }
    },
    /* 首页*/
    {
      path: '/',
      name: 'HelloWorld',
      components:{
        com :() => import('@/components/HelloWorld')
      },
      meta: {
          keepAlive: true,
          rank : 1
         }
    },
    //我的文章和收藏
    {
      path: '/articleAndCol',
      name: 'articleAndCol',
      components:{
        // com :() => import('@/components/utils/articleAndCol')
		com :() => import('@/components/utils/listForMe')
      },
      meta: {
        keepAlive: true,
         }
    },
    // 关注和粉丝
    {
      path: '/folAndFans',
      name: 'folAndFans',
      components:{
        com :() => import('@/components/me/utils/FolAndFans')
      },
      meta: {
          keepAlive: true,
         }
    },
    // 竞标
    {
      path: '/Bidding',
      name: 'Bidding',
      components:{
        com :() => import('@/components/utils/Bidding')
      },
      meta: {


         }
    },
    // 其他用户信息
    {
      path: '/otherUser',
      name: 'otherUser',
      components:{
        com :() => import('@/components/otherUser/OtherUser')
      },
      meta: {
          keepAlive: true,

         }
    },

    // 消息
    {
      path: '/info',
      name: 'info',
      components:{
        com :() => import('@/components/info/Info')
        // com :() => import('@/components/info/MyInfo')
      },
      meta: {
          keepAlive: true,

         }
    },
    /* 用户 user*/
    {
      path: '/user',
      name: 'me',
      components:{
        // com :() => import('@/components/me/Me')
        com :() => import('@/components/me/utils/My')
      },
      meta: {

         }
    },
	/* 登录 */
	{
	  path: '/login',
	  name: 'me',
	  components:{
	    // com :() => import('@/components/me/utils/Login')
      com :() => import('@/components/me/utils/phoneNum')
	  },
	  meta: {

	     }
	},
    /* 修改资料*/
    {
      path: '/userData',
      name: 'userData',
      components:{
        com :() => import('@/components/me/UserData')
      },
      meta: {

         }
    },
    /* 设置*/
    {
      path: '/setUp',
      name: 'setUp',
      components:{
        com :() => import('@/components/me/SetUp')
      },
      meta: {

         }
    },
    //文章发布页
    {
      path: '/editor',
      name: 'editor',
      // component: HelloWorld,
      components:{
        com :() => import('@/components/editor/Editor')
      },
      meta: {

         }
    },
    //GetArticleReward页面
    {
      path: '/GetArticleReward',
      name: 'GetArticleReward',
      // component: HelloWorld,
      components:{
        com :() => import('@/components/article/GetArticleReward')
      },
      meta: {
          keepAlive: true,
         }
    },
    //阅读文章页面
    {
      path: '/read',
      name: 'read',
      // component: HelloWorld,
      components:{
        com :() => import('@/components/article/Read')
      },
      meta: {
          keepAlive: true,
         }
    },
    //glate
    {
      path: '/plate',
      name: 'plate',
      // component: HelloWorld,Plus
      components:{
        com :() => import('@/components/plate/Plate')
      },
      meta: {
          keepAlive: true,
          rank : 2
         }
    },
    {
      path: '/search',
      name: 'search',
      // component: HelloWorld,
      components:{
        com :() => import('@/components/search/Search')
      },
      meta: {
          keepAlive: true
         }
    },
    {
      path: '/notFound',
      name: 'notFound',
      // component: HelloWorld,
      components:{
        com :() => import('@/components/utils/NotFound')
      },
      meta: {
          keepAlive: true
         }
    }
    //////////////////////////////////////////////////////////////////
  ]
})

// 全局路由
router.beforeEach( ( to,from,next )=>{
  /* 跳转路由时候 隐藏缓冲*/
  store._modulesNamespaceMap['win/'].context.commit('isDoingHandler', false)
  store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', false)
  /* 记录from的path给缓存组件参考*/
  store._modulesNamespaceMap['path/'].context.commit('fromPathHandler',from.path)
  // 将滚动条移到最上
  // document.documentElement.scrollTop  = 0
//////////////////////////////////////////////////////
        if(localStorage.getItem("token")){
          //是否第一次进入网页并且有token,如果是将发起网络请求获取name和img
          if(store._modulesNamespaceMap['login/'].context.state.HasTokenThenFirst == true){
            // 将用户登录状态为true
            store._modulesNamespaceMap['login/'].context.commit('hasLoginHandler',true)
            store._modulesNamespaceMap['login/'].context.commit('HasTokenThenFirstHandler', false)
            //发起网络请求获取name和img
            // let params = {}
            api.login.getUserInfo({}).then(
                res =>{
                    switch(res.data){
                      case "No_information":
                          Toast('没有此id信息');
                          break;
                      default:
                      Toast('登录成功');
                     store._modulesNamespaceMap['login/'].context.commit('userInfoHandler', res.data);
                    }
                }
            )
          }
        }
          next();
//////////////////////////////////////////////////////////
})


export default router



