
// 顶部导航栏和其他所有的组件通信
export default{
    namespaced:true,//带命名空间
    state:{
      // 是否是修改状态
      moBoolean : false,
      articleContent : ''
    },

    // params
    mutations:{
      moBooleanHandler(state, params){
        state.moBoolean = params
      },
      articleContentHandler(state, params){
        state.articleContent = params
      },

    },
    actions:{

    },
    getters:{

    }

}



